<template lang="html">
  <div class="">
    <bar-header title=""></bar-header>
    <div class="bar bar-subheader padding-left">
      
      <span class="badge badge-dark"></span>
    </div>
    <div class="scroll-content has-subheader">
      <div class="bar bar-header  tab-header">
        <tab active-color="#222">
          <tab-item>7</tab-item>
          <tab-item>30</tab-item>
        </tab>
      </div>
      <div class="scroll-content has-header">
        <scroller :lock-x="true" height="100%" v-ref:scroller>
          <div class="">
            <div class="flex wrap light-bg">
              <info-item v-for="item of infoItems" :title="item.title" :desc="item.desc"></info-item>
            </div>
            <div class="margin-top light-bg padding">
              <h6></h6>
            </div>
          </div>
        </scroller>
      </div>
    </div>
  </div>
</template>

<script>
import BaseView from './BaseView'
import InfoItem from '../components/InfoItem'

export default BaseView.extend({
  data: function () {
    return {
      infoItems:[
        {title:'',desc:'0.00'},
        {title:'',desc:'0'},
        {title:'',desc:'0'},
        {title:'',desc:'0.00'},
      ]
    }
  },
  computed: {},
  ready: function () {},
  attached: function () {},
  methods: {},
  components: {
    InfoItem
  }
})
</script>

<style lang="scss" scoped>

  .tab-header{
    display: block;
    padding:0;
  }
  .bar-subheader{
    font-size: 12px;
    border-bottom:1px solid #eaeaea;
    padding-right:100px;
    line-height: 1.4;
    .badge:last-child{
      position:absolute;
      top:10px;
      right:10px;
    }
  }
</style>



